body {
  margin: 0;
  padding: 0;
  font-family: "Arial", "Helvetica Neue", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --off-white: #fafafa;
}

.app {
  border: 2px solid gold;
}


h1 {
  font-family: 'Helvetica Neue', 'Arial';
}
p {
  line-height: 1.5em;
}

h1 > .section {
  margin: 20;
  padding: 20;
}




.nav {
    background-color: var(--off-white);
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    width: 100%;
    height: 80px;
    z-index: 1000;
  
  }
  
  h3.nav {
    cursor:  pointer;
  }
  
  .nav-logo {
    min-height: 1em;
  }
  
  .nav-logo:hover {
    cursor: pointer;
    background: linear-gradient(to bottom, 
                                  rgba(220, 220, 220, 0) 0%,
                                  rgba(220, 220, 220, 0) 90%, 
                                  skyblue 90%, 
                                  skyblue 100%); 
  }
  
  .nav .nav-content {
    padding: 0rem 3rem;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
  }
  
  .nav-item {
    display: inline;
    margin-left: 2rem;
    font-family: 'Arial', 'Helvetica Neue';
    color: #666;
  }
  
  .nav-item:hover {
    color: black;
    cursor: pointer;
  }
  
  .nav-item > .activeGold {
    border-bottom: 2px solid gold;
  
  }
  
  .nav-item > .activePurple {
    border-bottom: 2px solid crimson;
  
  }
:root {
    --card-width: 600;
}
a.card {
    margin: 0 auto;
}

.card {
    width: 600;
    width: var(--card-width);
    height: calc(600 + 20);
    height: calc(var(--card-width) + 20);
    padding: 0px 10px;
    margin: 0 auto;
    background-color:#FFF;
    float: left;
    color: black;
    background: linear-gradient(to bottom, rgba(220, 220, 220, 0) 0%, rgba(220, 220, 220, 0) 62%, gold 62%, gold 100%);
    opacity: 0.9;
    -webkit-animation: fade-slide-down .2s ease-out forwards;
            animation: fade-slide-down .2s ease-out forwards;
}

.card:before {
    background: linear-gradient(to bottom, rgba(220, 220, 220, 0) 0%, rgba(220, 220, 220, 0) 62%, gold 62%, gold 100%);
}

.card:hover {
  
  -webkit-animation: fade-slide-up .2s ease-out forwards;
  
          animation: fade-slide-up .2s ease-out forwards;
}

  @-webkit-keyframes fade-slide-up {
    0% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
  }

  @keyframes fade-slide-up {
    0% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
    100% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
  }

  @-webkit-keyframes fade-slide-down {
    0% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
    100% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
  }

  @keyframes fade-slide-down {
    0% {
      opacity: 1;
      -webkit-transform: translateY(-1rem);
              transform: translateY(-1rem);
    }
    100% {
      opacity: 0.8;
      -webkit-transform: none;
              transform: none;
    }
  }
